import React, { useContext, useState } from 'react'
import { Link, useHistory } from 'react-router-dom'
import ArticleContext from '../context/Article.context'
import {
    Button,
    Card,
    CardHeader,
    CardContent,
    IconButton,
    List,
    ListItem,
    ListItemText,
    ListItemSecondaryAction,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogContentText,
    DialogActions
} from '@material-ui/core'
import { Add as AddIcon, Delete as DeleteIcon } from '@material-ui/icons'

const DeleteDialog = ({ open, handleClose, handleDeleteArticle }) => (
    <Dialog
        open={open}
        onClose={handleClose}>
        <DialogTitle>
            Delete Alert
        </DialogTitle>
        <DialogContent>
            <DialogContentText>
                Are you sure want to delete this article?
            </DialogContentText>
        </DialogContent>
        <DialogActions>
            <Button onClick={handleClose} color="primary" autoFocus>
                Disagree
            </Button>
            <Button color="secondary" onClick={handleDeleteArticle}>
                Agree
            </Button>
        </DialogActions>
    </Dialog>
)

const ArticleList = ({ activeId }) => {
    const { articles, deleteArticle } = useContext(ArticleContext)
    const [open, setOpen] = useState(false)
    const [activeArticle, setActiveArticle] = useState('')

    const history = useHistory()

    const handleDeleteArticle = id => {
        if (id) {
            setOpen(true)
            setActiveArticle(id)
        } else {
            deleteArticle(activeArticle)
            setActiveArticle('')
            history.push('/')
        }
    }

    return (
        <React.Fragment>
            <DeleteDialog
                open={open}
                handleClose={() => setOpen(false)}
                handleDeleteArticle={() => handleDeleteArticle(null)} />

            <Card>
                <CardHeader
                    title="Articles"
                    action={
                        <Link to="/add">
                            <IconButton>
                                <AddIcon />
                            </IconButton>
                        </Link>
                    }
                />
                <CardContent>
                    <List dense>
                        {articles.length ?
                            articles.map((article, key) => (
                                <ListItem
                                    key={key}
                                    button
                                    selected={activeId === article.id}
                                    onClick={() => history.push(`/${article.id}`)}>
                                    <ListItemText
                                        primary={article.title}
                                        secondary={article.body}
                                        secondaryTypographyProps={{ noWrap: true }} />
                                    <ListItemSecondaryAction>
                                        <IconButton edge="end" aria-label="delete" onClick={() => handleDeleteArticle(article.id)}>
                                            <DeleteIcon />
                                        </IconButton>
                                    </ListItemSecondaryAction>
                                </ListItem>
                            )) :
                            (<p>No data found</p>)
                        }
                    </List>
                </CardContent>
            </Card>
        </React.Fragment>
    )
}

export default ArticleList
